.about__section {
  padding-top: 126px;
  background-color: var(--section-bg);
  @media screen and (max-width: 767px) {
    background-image: url(/images/about/about-background-image-mobile.png);
    background-position: center bottom 60px;
    background-repeat: no-repeat;
    background-size: 100% 133px;
    // background-size: cover;
  }
  @media (min-device-pixel-ratio: 2) and (max-width: 767px),
    (min-resolution: 192dpi) and (max-width: 767px),
    (min-resolution: 2dppx) and (max-width: 767px) {
    background-image: url(/images/about/about-background-image-mobile@2x.png);
  }

  @media screen and (min-width: 1280px) {
    background-image: url(/images/about/about-background-image-desktop.png);

    background-position: center bottom;
    background-repeat: no-repeat;
    background-size: 100% 534px;
  }
  @media (min-device-pixel-ratio: 2) and (min-width: 1280px),
    (min-resolution: 192dpi) and (min-width: 1280px),
    (min-resolution: 2dppx) and (min-width: 1280px) {
    background-image: url(/images/about/about-background-image-desktop@2x.png);
  }
}

.about {
  &__subtitle {
    &::selection {
      color: var(--white-color);
      background-color: var(--hero-header-bg);
    }
    font-family: var(--title-font);
    @include font(18px, 400, 1.16, 0.04em);
    text-align: center;
    text-transform: uppercase;
    color: var(--label-color);
    margin-bottom: 16px;

    @media screen and (min-width: 768px) {
      @include font(26px, 400, 1.15, 0.04em);
    }

    @media screen and (min-width: 1280px) {
      @include font(30px, 400, 1.13, 0.04em);
    }
  }

  &__title {
    &::selection {
      color: var(--white-color);
      background-color: var(--hero-header-bg);
    }
    font-family: var(--title-font);
    @include font(34px, 400, 1.15, 0.04em);
    text-align: center;
    text-transform: uppercase;
    color: var(--accent-color);
    margin-bottom: 26px;

    @media screen and (min-width: 768px) {
      @include font(48px, 400, 1.15, 0.04em);
      margin-bottom: 50px;
    }

    @media screen and (min-width: 1280px) {
      @include font(58px, 400, 1.14, 0.06em);
      margin-bottom: 56px;
    }
  }
}

.about__box-description {
  @media screen and (min-width: 768px) {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    margin-top: -20px;
    margin-left: -20px;
  }

  @media screen and (min-width: 1280px) {
    margin-top: -30px;
    margin-left: -30px;
    padding-bottom: 56px;
  }
}

.about__text {
  &::selection {
    color: var(--white-color);
    background-color: var(--hero-header-bg);
    font-size: 12px;

    @media screen and (min-width: 768px) {
      font-size: 14px;
    }
  }

  @include font(12px, 500, 1.62, 0.03em);
  padding-left: 30px;
  padding-right: 30px;
  margin-bottom: 14px;
  text-align: left;
  color: #907e82;

  @media screen and (min-width: 768px) {
    @include font(14px, 400, 1.76, 0.03em);
    padding: 0;
  }
  @media screen and (min-width: 1280px) {
    @include font(16px, 500, 1.76, 0.04em);
    margin-bottom: 14px;
    padding-left: 42px;
  }
}

.about__text_color {
  margin-bottom: 32px;
  color: var(--black-color);

  @media screen and (min-width: 768px) {
    padding-top: 52px;
  }
}

.about__left-side {
  @media screen and (max-width: 767px) {
    margin-bottom: 46px;
  }

  @media screen and (min-width: 768px) {
    margin-top: 20px;
    margin-left: 20px;
    flex-basis: calc((100% - 40px) / 2);
  }
}

.about__right-side {
  &:first-child {
    margin-bottom: 32px;
    color: var(--black-color);

    @media screen and (min-width: 768px) {
      padding-top: 52px;
    }
  }

  @media screen and (min-width: 768px) {
    margin-top: 20px;
    margin-left: 20px;
    flex-basis: calc((100% - 40px) / 2);
  }
}
